export const required = value => {
    if (value) return undefined;
    return "Fieled is required";
}

export const maxLenghtCreator = (maxLength) => (value) => {
    if (value.length > maxLength) return `Max length is ${maxLength} symbol`;
    return undefined;
}

export const minLenghtCreator = (minLength) => (value) => {
    if (value.length < minLength) return `Min length is ${minLength} symbol`;
    return undefined;
}