import React from 'react';
import './App.css';
import Navbar from './components/Navbar/Navbar';
import {HashRouter, Route, withRouter} from "react-router-dom";
import News from "./components/News/News";
import Music from "./components/Music/Music";
import Setting from "./components/Setting/Setting";
import ProfilesContainer from "./components/Profiles/ProfilesContainer";
import HeaderContainer from "./components/Header/HeaderContainer";
import Login from "./components/Login/Login";
import {connect, Provider} from "react-redux";
import {compose} from "redux";
import {initializeApp} from "./redux/app-reducer";
import Preloader from "./components/common/Preloader";
import store from "./redux/redux-store";
import {withSuspense} from "./components/Hoc/withSuspense";

const DialogsContainer = React.lazy (() => import('./components/Dialogs/DialogsContainer'));
const UsersContainer = React.lazy (() => import('./components/Users/UsersContainer'));
const FriendsContainer = React.lazy (() => import('./components/Friends/FriendsContainer'));


class App extends React.Component {
    componentDidMount() {
        this.props.initializeApp();
    }
    render() {
        if (!this.props.initialized) {
            return <Preloader/>
        }
        return (
            <div className='app-wrapper'>
                <HeaderContainer/>

                <Navbar/>

                <div className='app-wrapper-content'>
                    <Route path='/dialogs' render={() => {
                        return <React.Suspense fallback={<div>Loding...</div>}>
                        <DialogsContainer/>
                        </React.Suspense>
                    }}/>
                    <Route path='/profile/:userId?' render={() => <ProfilesContainer/>}/>
                    <Route path='/news' render={() => <News/>}/>
                    <Route path='/music' render={() => <Music/>}/>
                    <Route path='/setting' render={() => <Setting/>}/>
                    <Route path='/users' render={() => {
                        return <React.Suspense fallback={<div>Loding...</div>}>
                        <UsersContainer/>
                        </React.Suspense>
                    }}/>
                    <Route path='/login' render={() => <Login/>}/>
                    <Route path='/friends' render={withSuspense(FriendsContainer)} />
                </div>

            </div>
        );
    }
}

const mapStateToProps = (state) => ({
    initialized: state.app.initialized
})

let AppContainer = compose(
    withRouter,
    connect(mapStateToProps, {initializeApp}))(App);

let SamuraiApp = (props) =>{
    return (
    <HashRouter>
        <Provider store={store}>
            <React.StrictMode>
                < AppContainer />
            </React.StrictMode>,
        </Provider>
    </HashRouter>)
}

export default SamuraiApp;


