import React from 'react';
import s from './Header.module.css';
import {NavLink} from "react-router-dom";
import panda from '../../assets/images/panda_box.jpg';
import supermen from '../../assets/images/super.png'


const Header = (props) => {
    return <header className={s.header}>
        <img className={s.superLogo} src={supermen}></img>
        <div className={s.loginBlock}>
            <div>
                {props.isAuth ? <div>
                        <button className={s.loginButton} onClick={props.Logout}>Log out</button>
                    </div>
                    : <div>
                        <NavLink to={'/login'}><button className={s.loginButton}>Login</button></NavLink>
                    </div>}

            </div>
            <div>
                <img className={s.pandaImg} src={panda}/>
            </div>

            <div>
                <div>
                    Name: {props.login}
                </div>
                <div>id: {props.id}</div>
                <div>email: {props.email}</div>

            </div>

        </div>
    </header>

}

export default Header;