const SEND_MESSAGE = 'SEND-MESSAGE';

let initialState = {
    messages: [
        {id: 1, message: 'Hi'},
        {id: 2, message: 'You'},
        {id: 3, message: 'Thanks'},
        {id: 4, message: 'Goodbey'},
        {id: 5, message: 'Hello'},
       /* {id: 6, message: 'Goognight'},*/
    ],


    dialogs: [
        {id: 1, name: 'Dimych'},
        {id: 2, name: 'Michael'},
        {id: 3, name: 'Tanja'},
        {id: 4, name: 'Vasilisa'},
        {id: 5, name: 'Kostja'},
       /* {id: 6, name: 'Sofia'},*/
    ],
};

const dialogsReducer = (state = initialState, action) => {

    switch (action.type) {
        case SEND_MESSAGE:
            let body = action.newMessageBody;
                 return  {
                     ...state,
                     messages: [...state.messages, {id:7, message: body}]
                 };

        default:
            return state;
    }
}


export const sendMessageActionCreator = (newMessageBody) => {
    return {type: SEND_MESSAGE, newMessageBody}
}

export default dialogsReducer;