import React, {useState} from 'react';
import s from './ProfileInfo.module.css';
import Preloader from "../../../common/Preloader";
import jobPhoto from '../../../../assets/images/smail-1.png'
import jobPhoto2 from '../../../../assets/images/smail-2.png'
import ProfileStatusWithHooks from "./ProfileStatusWithHooks";
import userPhoto from "../../../../assets/images/user-icon.png";
import ProfileDataFormReduxForm from "./ProfileDataForm";


const ProfileInfo = ({profile, status, updateStatus, isOwner, savePhoto, saveProfile}) => {

    let [editMode, setEditMode] = useState(false);

    if (!profile) {
        return <Preloader/>
    }

    const onMainPhotoSelected = (e) => {
        if (e.target.files.length) {
            savePhoto(e.target.files[0]);
        }
    }

    const onSubmit = (formData) => {
        saveProfile(formData).then(
            () => {
                setEditMode(false);
            }
        );
    }

    return (
        <div>

            <div className={s.userInfo}>
                <div className={s.photoLarge}>
                    <img src={profile.photos.large || userPhoto} className={s.myPhoto}></img>
                    <div>
                        {isOwner && <input className={s.savePhoto} type={"file"} onChange={onMainPhotoSelected}/>}
                    </div>
                    <div>
                        <ProfileStatusWithHooks status={status} updateStatus={updateStatus}/>
                    </div>

                </div>
                <div>
                    {editMode
                        ? <ProfileDataFormReduxForm initialValues={profile} profile={profile} onSubmit={onSubmit}/>
                        : <ProfileData goToEditMode={() => {
                            setEditMode(true)
                        }} profile={profile} isOwner={isOwner}/>}
                </div>
            </div>

        </div>)
}

const ProfileData = ({profile, goToEditMode, isOwner}) => {
    return (
        <div>
            <div className={s.blok_1}>
                <div className={s.text}>Обо мне: {profile.aboutMe}</div>
                <div className={s.text}>Полное имя: {profile.fullName}</div>
                <div className={s.text}>Мой Id: {profile.userId}</div>
            </div>

            <div>
                <img src={profile.lookingForAJob ? jobPhoto : jobPhoto2}
                     className={s.userPhoto}/>
            </div>
            <div className={s.blok_3}>Как я ищу работу: {profile.lookingForAJobDescription}</div>

            {isOwner && <div>
                <button className={s.buttonContact} onClick={goToEditMode}>Edit</button>
            </div>}

            <div className={s.blok_2}>
                <b className={s.contact_1}>Contacts</b>: {Object.keys(profile.contacts).map(key => {
                return <Contact key={key} contactTitle={key} contactValue={profile.contacts[key]}/>
            })}
            </div>
            <div>
                <img className={s.smallPhoto} src={profile.photos.small}/>
            </div>
        </div>

    )
}


const Contact = ({contactTitle, contactValue}) => {
    return <div className={s.contact}><b>{contactTitle}</b>: {contactValue}</div>
}

export default ProfileInfo;