import React from 'react';
import s from './Posts.module.css';

const Posts = (props) => {
    return (
      
        <div className={s.ithem}>
            <img src='https://img2.goodfon.ru/wallpaper/nbig/a/d2/wolverine-james-howlett-logan.jpg'></img>
            {props.message}<span> {props.likecount} Like</span>
        </div>
        )

}

export default Posts;