import React from 'react';
import jobPhoto from "../../../../assets/images/smail-1.png";
import jobPhoto2 from "../../../../assets/images/smail-2.png";
import s from "./ProfileInfo.module.css";
import {Field, reduxForm} from "redux-form";
import {Input, Textarea} from "../../../common/FormsControl/FormsControls";
import handleSubmit from "redux-form/lib/handleSubmit";




const ProfileDataForm = ({profile, handleSubmit, error}) => {
    return (
        <form onSubmit={handleSubmit}>
            <div>
                <b>Обо мне: </b>: {profile.aboutMe}
                {createField("About Me", "aboutMe", [], Textarea)}
                </div>
            <div><b>Full name</b>: {createField("Full name", "fullName", [], Input)}</div>
            <div>Мой Id: {profile.userId}</div>
            <div>
                <img src={profile.lookingForAJob ? jobPhoto : jobPhoto2}
                     className={s.userPhoto}/>
            </div>
            <div>
                <b>Looking for a job:</b>: {profile.lookingForAJob ? "yes" : "no"}
                { createField("", "lookingForAJob", [], Input, {type: "checkbox"})}
            </div>
            <div>
                <b>My professional skils</b>: {profile.lookingForAJobDescription}
                {createField("My professional skils", "lookingForAJobDescription", [], Textarea)}
               </div>
            <div>
                <img src={profile.photos.small}/>
            </div>

            <div><button>Save</button></div>
            {error && <div className={s.formSummaryError}>
                {error}
            </div>}

            <div>
                <b>Contacts</b>: {Object.keys(profile.contacts).map(key => {
                return <div key={key} className={s.conacts}>
                    <b>{key}: {createField(key, "contacts." + key, [], Input)}</b></div>
            })}
            </div>
        </form>

    )
}

const ProfileDataFormReduxForm = reduxForm({form: 'edit-profile'})(ProfileDataForm);

export const createField = (placeholder, name, validators, component, props = {}, text = "") => (
    <div>
        <Field placeholder={placeholder} name={name} validators={validators}
               component={component} {...props} /> {text}
    </div>
)

export default ProfileDataFormReduxForm;