import React from 'react';
import s from './MyPosts.module.css';
import Posts from './Post/Posts';
import {Field, reduxForm} from "redux-form";
import {maxLenghtCreator, required} from "../../../Utils/Validators/validators";
import {Textarea} from "../../common/FormsControl/FormsControls";

const maxLength10 = maxLenghtCreator(10);

const MyPosts = (props) => {

    let postsElements = [...props.posts].reverse().map(p => <Posts key={p.id} message={p.message} likecount={p.likescount}/>);

    let AddPostF = (values) => {
        props.addPost(values.newPostText);
    }
    let newPostElement = React.createRef();

    return (
        <div className={s.postsBlock}>
            <h3>My post</h3>
            <div>

                <AddPostReduxForm onSubmit={AddPostF}/>

            </div>
            <div className={s.posts}>

                {postsElements}

            </div>
        </div>)
}
const AddPostForm = (props) => {
    return (
        <form onSubmit={props.handleSubmit}>
            <div>
                <Field name={"newPostText"} component={Textarea} placeholder={"Enter your post"}
                       validate={[required, maxLength10]}/>
            </div>
            <div>
                <button>Add Post</button>
            </div>
        </form>
    )
}
const AddPostReduxForm = reduxForm({form:"profileAddPost"})(AddPostForm)

export default MyPosts;