import React from 'react';
import s from './Navbar.module.css';
import {NavLink} from "react-router-dom";
import NavbarFr from "./NavbarFr/NavbarFr";

const Navbar = (props) => {

    return <nav className={s.nav}>
        <div className={s.ithem}>
            <NavLink to={'/profile'} activeClassName={s.activeLink}>Profiles</NavLink>
        </div>
        <div className={s.ithem}>
            <NavLink to={'/dialogs'} activeClassName={s.activeLink}>Messages</NavLink>
        </div>
        <div className={s.ithem}>
            <NavLink to={'/news'} activeClassName={s.activeLink}>News</NavLink>
        </div>
        <div className={s.ithem}>
            <NavLink to={'/music'} activeClassName={s.activeLink}>Music</NavLink>
        </div>
        <div className={s.ithem}>
            <NavLink to={'/setting'} activeClassName={s.activeLink}>Setting</NavLink>
        </div>
        <div className={s.ithem}>
            <NavLink to={'/users'} activeClassName={s.activeLink}>Users</NavLink>
        </div>
        <div className={s.ithem}>
            <NavLink to={'/login'} activeClassName={s.activeLink}>Login</NavLink>
        </div>
        <div className={s.ithem}>
            <NavLink to={'friends'} activeClassName={s.activeLink}>Friends</NavLink>
        </div>

    </nav>

}

export default Navbar;