const SEND_FRIENDS = 'SEND-FRIENDS';

let initialState = {
    friends: [
        {id: 1, name: 'Mchael'},
        {id: 2, name: 'Tanja'},
        {id: 3, name: 'Vasilisa'},
    ],
    friendsMess: [
        {id: 1, message: 'Big Boss'},
        {id: 2, message: 'Hot Girls'},
        {id: 3, message: 'Uru-ru'},
    ]

};

const sidebarReducer = (state = initialState, action) => {
    switch (action.type) {
        case SEND_FRIENDS:
            let friendsM = action.newMessageFriends;
            return {
                ...state,
                friendsMess: [...state.friendsMess, {id: 4, message:friendsM }]
            }
        default:
            return state;
    }
}

export const sendFriendsActionCreator = (newMessageFriends) => {
    return {type: SEND_FRIENDS, newMessageFriends}
}

export default sidebarReducer;